import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Geolocation } from '@ionic-native/geolocation';
import { GoogleMaps, GoogleMap, GoogleMapsEvent, GoogleMapOptions } from '@ionic-native/google-maps';
import { ThrowStmt } from '@angular/compiler';
import { updateDate } from 'ionic-angular/umd/util/datetime-util';

/**
 * Generated class for the TestPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-test',
  templateUrl: 'test.html',
})
export class TestPage {

  map: GoogleMap;
  mapElement: HTMLElement;

  watchId: any;
  latitude: any;
  longitude: any;

  zoomValue: any;
  tiltValue: any;
  bearingValue: any;



  constructor(public navCtrl: NavController, public navParams: NavParams, private geo: Geolocation, public googleMaps: GoogleMaps) {
    this.zoomValue = 18;
    this.tiltValue = 60;
    this.bearingValue = 140;
  }
  

loadMap(){

      try {

        this.mapElement = document.getElementById('map');

        let mapOption: GoogleMapOptions = {
          camera:{
            target:{
              lat: 32.8371608,
              lng:-117.1430907
            },
            zoom: 18,
            tilt: 30
          }
        };

        this.map = this.googleMaps.create(this.mapElement , mapOption);

        this.map.one(GoogleMapsEvent.MAP_READY).then(
          () => {

            alert("Map is Ready to use");

            this.map.addMarker({
              title : 'Hi from ionic',
              icon: 'blue',
              animation: 'DROP',
              position:{
                lat: 32.8371608,
                lng:-117.1430907
              }
            }).then(marker => {
              marker.on(GoogleMapsEvent.MARKER_CLICK).subscribe(() =>
                {
                  alert("Hi there...");
                }
              );
            });
          }
        );
        
      } catch (error) {

        alert("Map Error" + error);
        
        
      }

}

startGeolocation(){
    let geoOptions = { enableHighAccuracy: true };

    this.geo.watchPosition(geoOptions).subscribe(data =>{

        try {

          this.latitude = data.coords.latitude;
          this.longitude = data.coords.longitude;

          this.map.addMarker({
              title: 'Hello from Ionic',
              icon: 'red',
              animation: 'DROP',
              position: {
                lat: this.latitude,
                lng: this.longitude
              }
          });

          var GOOGLE = {"lat": this.latitude, "lng": this.longitude};
          this.map.moveCamera({

            'target': GOOGLE,
            'tilt': this.tiltValue,
            'zoom': this.zoomValue,
            'bearing': this.bearingValue
          });
          
        } catch (error) {
          alert("Geo Error" + error);
        }
    });

}

stopGeolocation(){

}

setBearing(bearing){
  this.bearingValue.bearingValue = bearing;
  this.updateMap();
}

setTilt(tilt){
  this.tiltValue = tilt;
  this.updateMap();
}

setZoom(zoom){
  this.zoomValue = zoom;
  this.updateMap();
}

updateMap(){
  var GOOGLE = {"lat": this.latitude, "lng": this.longitude};
  this.map.moveCamera({

    'target': GOOGLE,
    'tilt': this.tiltValue,
    'zoom': this.zoomValue,
    'bearing': this.bearingValue
  });
}



}



