import {Component} from "@angular/core";
import {NavController, PopoverController, ToastController} from "ionic-angular";
import {Storage} from '@ionic/storage';

import {NotificationsPage} from "../notifications/notifications";
import {SettingsPage} from "../settings/settings";
import {TripsPage} from "../trips/trips";
import {SearchLocationPage} from "../search-location/search-location";

import { AngularFireAuth } from 'angularfire2/auth';
import { NewsPage } from "../news/news";
import { CategoriesPage } from "../categories/categories";
import { TestPage } from "../test/test";
import { ProfilePage } from "../profile/profile";


@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})

export class HomePage {
  // search condition
  public search = {
    name: "Rio de Janeiro, Brazil",
    date: new Date().toISOString()
  }

  tab1Root = NewsPage;
  tab2Root = CategoriesPage;
  tab3Root = ProfilePage;

  constructor(private storage: Storage, public nav: NavController, public popoverCtrl: PopoverController, private toast:ToastController, private fireAuth: AngularFireAuth,) {
  }

  // ionViewWillLoad(){
  //   this.fireAuth.authState.subscribe(data => {
  //     if(data.email){
  //       this.toast.create({
  //         message: 'Welcome to Hoputa Monitoring',
  //         duration: 4000,
  //         cssClass: "welcome"
  //       }).present();
  //     }
  //     else{
  //       this.toast.create({
  //         message: 'Could Not Find Authentication details',
  //         duration: 3000
  //       }).present();
  //     }
      
  //   });
  // }
 
  ionViewWillEnter() {
    // this.search.pickup = "Rio de Janeiro, Brazil";
    // this.search.dropOff = "Same as pickup";
    this.storage.get('pickup').then((val) => {
      if (val === null) {
        this.search.name = "Rio de Janeiro, Brazil"
      } else {
        this.search.name = val;
      }
    }).catch((err) => {
      console.log(err)
    });
  }



  // go to result page
  doSearch() {
    this.nav.push(TripsPage);
  }

  // choose place
  choosePlace(from) {
    this.nav.push(SearchLocationPage, from);
  }

  // to go account page
  goToAccount() {
    this.nav.push(SettingsPage);
  }

  presentNotifications(myEvent) {
    console.log(myEvent);
    let popover = this.popoverCtrl.create(NotificationsPage);
    popover.present({
      ev: myEvent
    });
  }

  filter() {
    this.nav.push(TripsPage);
  }

}

