import { Component, ViewChild, ElementRef } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { GESTURE_PRIORITY_TOGGLE } from 'ionic-angular/umd/gestures/gesture-controller';
import { Geolocation } from '@ionic-native/geolocation';

declare var google: any;

@IonicPage()
@Component({
  selector: 'page-map',
  templateUrl: 'map.html',
})
export class MapPage {

  @ViewChild('map') mapElement: ElementRef;
@ViewChild('directionsPanel') directionsPanel: ElementRef;
map: any;
here: any;
lat: any;
lng: any;

constructor(public navCtrl: NavController, public geolocation: Geolocation) {
  console.log(this.mapElement);
}

ionViewDidLoad(){
this.loadMap();
this.startNavigating();
}

loadMap(){
this.geolocation.getCurrentPosition().then((position) => {
this.here = new google.maps.LatLng(position.coords.latitude, position.coords.longitude);
let mapOptions = {
center: this.here,
zoom: 15,
mapTypeId: google.maps.MapTypeId.ROADMAP
}
this.map = new google.maps.Map(this.mapElement.nativeElement, mapOptions);
}, (err) => {
console.log(err);
})
}

addMarker(){
let marker = new google.maps.Marker({
map: this.map,
animation: google.maps.Animation.DROP,
position: this.map.getCenter()
});
let content = "<h4>Information!</h4>"; 
this.addInfoWindow(marker, content);
}

addInfoWindow(marker, content){
let infoWindow = new google.maps.InfoWindow({
content: content
});
google.maps.event.addListener(marker, 'click', () => {
infoWindow.open(this.map, marker);
});
}

startNavigating(){
console.log('Navigation Started!');
this.geolocation.getCurrentPosition().then((position) => {
let directionsService = new google.maps.DirectionsService;
let directionsDisplay = new google.maps.DirectionsRenderer;
directionsDisplay.setMap(this.map);
directionsDisplay.setPanel(this.directionsPanel.nativeElement);
directionsService.route({
origin: this.here,
destination: {lat: 9.956970, lng: 8.890165}, 
travelMode: google.maps.TravelMode['DRIVING']
}, (res, status) => {
if(status == google.maps.DirectionsStatus.OK){
console.log('Displaying directions!');
directionsDisplay.setDirections(res);
} else {
console.log('Directions failed! Sorry!');
console.warn(status);
}
});
})
}
}

  



