import {Component} from "@angular/core";
import {NavController, AlertController, ToastController, MenuController, LoadingController} from "ionic-angular";
import {HomePage} from "../home/home";
import {RegisterPage} from "../register/register";

import { User } from '../../models/user';
import { AngularFireAuth } from 'angularfire2/auth';

@Component({
  selector: 'page-login',
  templateUrl: 'login.html'
})
export class LoginPage {

  user = {} as User;

  loading: any;

  constructor(public nav: NavController, public forgotCtrl: AlertController, public menu: MenuController, public toastCtrl: ToastController, private fireAuth: AngularFireAuth, public navCtrl: NavController, private toast:ToastController, public loadingCtrl: LoadingController) {
    this.menu.swipeEnable(false);
  }

  showLoader(){
    this.loading = this.loadingCtrl.create({
      content: 'Please wait Authenticating...'

    });

    this.loading.present();
  }

  // go to register page
  register() {
    this.nav.setRoot(RegisterPage);
  }

  // login and go to home page
  async login(user: User){
    try{
      this.showLoader();
      const info = await this.fireAuth.auth.signInWithEmailAndPassword(user.email, user.password);
      if(info){
        this.loading.dismiss();
        await this.navCtrl.setRoot(HomePage);
      }
    }
    catch(e){
      this.toast.create({
        message: "Invalid email or password",
        duration: 3000,
        cssClass: "error"
      }).present();
    }
  }

  forgotPass() {
    let forgot = this.forgotCtrl.create({
      title: 'Forgot Password?',
      message: "Enter you email address to send a reset link password.",
      inputs: [
        {
          name: 'email',
          placeholder: 'Email',
          type: 'email'
        },
      ],
      buttons: [
        {
          text: 'Cancel',
          handler: data => {
            console.log('Cancel clicked');
          }
        },
        {
          text: 'Send',
          handler: data => {
            console.log('Send clicked');
            let toast = this.toastCtrl.create({
              message: 'Email was sended successfully',
              duration: 3000,
              position: 'top',
              cssClass: 'dark-trans',
              closeButtonText: 'OK',
              showCloseButton: true
            });
            toast.present();
          }
        }
      ]
    });
    forgot.present();
  }

}



