import { Component, ViewChild } from "@angular/core";
import { Platform, Nav } from "ionic-angular";

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { Keyboard } from '@ionic-native/keyboard';

import { HomePage } from "../pages/home/home";
import { TestPage } from "../pages/test/test";
import { LoginPage } from "../pages/login/login";
import { LocalWeatherPage } from "../pages/local-weather/local-weather";
import { MapPage } from "../pages/map/map";
import { ReportIncidentPage } from "../pages/report-incident/report-incident";
import { AboutPage } from "../pages/about/about";
import { InecInfoPage } from "../pages/inec-info/inec-info";
import { ReportIncident2Page } from "../pages/report-incident2/report-incident2";

export interface MenuItem {
    title: string;
    component: any;
    icon: string;
}

@Component({
  templateUrl: 'app.html'
})

export class MyApp {
  @ViewChild(Nav) nav: Nav;

  rootPage: any = LoginPage;

  appMenuItems: Array<MenuItem>;

  constructor(
    public platform: Platform,
    public statusBar: StatusBar,
    public splashScreen: SplashScreen,
    public keyboard: Keyboard
  ) {
    this.initializeApp();

    this.appMenuItems = [
      {title: 'Home', component: HomePage, icon: 'home'},
      // {title: 'Test', component: TestPage, icon: 'create'},
      {title: 'Report Incident', component: ReportIncident2Page, icon: 'create'},
      {title: 'View Results', component: LocalWeatherPage, icon: 'eye'},
      {title: 'Locate Polling Unit', component: TestPage, icon: 'pin'},
      {title: 'View INEC Contact', component: InecInfoPage, icon: 'call'},
      {title: 'Local Weather', component: LocalWeatherPage, icon: 'partly-sunny'},
      // {title: 'Test', component: TestPage, icon: 'home'},
      {title: 'About App', component: AboutPage, icon: 'information-circle'}
    ];
  }

  initializeApp() {
    this.platform.ready().then(() => {
      // Okay, so the platform is ready and our plugins are available.

      //*** Control Splash Screen
      // this.splashScreen.show();
      // this.splashScreen.hide();

      //*** Control Status Bar
      this.statusBar.styleDefault();
      this.statusBar.overlaysWebView(false);

      //*** Control Keyboard
      // this.keyboard.disableScroll(true);
    });
  }

  openPage(page) {
    // Reset the content nav to have just this page
    // we wouldn't want the back button to show in this scenario
    this.nav.setRoot(page.component);
  }

  logout() {
    this.nav.setRoot(LoginPage);
  }

}



