import {Component} from "@angular/core";
import { IonicPage, NavController, NavParams, ToastController, LoadingController } from 'ionic-angular';
import {LoginPage} from "../login/login";
import {HomePage} from "../home/home";

import { User } from '../../models/user';
import { AngularFireAuth } from 'angularfire2/auth';

import { UserProvider } from '../../providers/user/user';

@Component({
  selector: 'page-register',
  templateUrl: 'register.html'
})
export class RegisterPage {

  user = {} as User;

  loading: any;

  newuser = {
    displayName: '',
    email: '',
    password: ''
  }

  constructor(private fireAuth: AngularFireAuth, public toastCtrl: ToastController, public navCtrl: NavController, public navParams: NavParams, public nav: NavController, public loadingCtrl: LoadingController, public userservice: UserProvider) {
  }

  showLoader(){
    this.loading = this.loadingCtrl.create({
      content: 'Registering User Please wait...'

    });

    this.loading.present();
  }

  // register and go to home page
    register(){
      var toaster = this.toastCtrl.create({
        duration: 3000,
        position: 'bottom'
      });
      if (this.newuser.email == '' || this.newuser.password == '' || this.newuser.displayName == ''){
      toaster.setMessage('All fields are required here');
      toaster.present();
    }
      else if(this.newuser.password.length < 6 ){
      toaster.setMessage('Password is not strong');
      toaster.present();
      }
      else {
      let loader = this.loadingCtrl.create({
        content: 'Registering User Please wait...'
      });
      loader.present();
      this.userservice.adduser(this.newuser).then((res: any) => {
        loader.dismiss();
        if(res.success)
        this.nav.push('ProfilepicPage');
        else
        alert('Error' + res);

      })
    } 
  }

  // go to login page
  login() {
    this.nav.setRoot(LoginPage);
  }
}



