import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { AngularFireDatabase } from 'angularfire2/database';

import { Observable } from 'rxjs/Observable';
import { Note } from '../../models/note/note.models';
import { NoteListService } from '../../services/note-list.service';

/**
 * Generated class for the NewsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-news',
  templateUrl: 'news.html',
})
export class NewsPage {

  comments;
  dataToAdd;
  dataToAdd2;

  noteList: Observable<Note[]>

  constructor(public navCtrl: NavController, public navParams: NavParams, public afd: AngularFireDatabase, private noteListService: NoteListService) {
    // this.getDataFromFireBase();

    this.noteList = this.noteListService.getNoteList()
      .snapshotChanges()
      .map(
      changes => {
        return changes.map(c => ({
          key: c.payload.key, ...c.payload.val()
        }))
      })
      .map(changes => changes.reverse());
  }

  // getDataFromFireBase() {
  //   this.afd.list('Incidents/').valueChanges().subscribe(
  //     data => {
  //       console.log(data)
  //       this.comments = data;
  //     }
  //   )
  // }

  addData() {
    this.afd.list('Incidents/').push(this.dataToAdd)
  }


}



